import { Components } from '@ionic/core';
import { defineCustomElement as defineCustomElementFab } from '@ionic/core/components/ion-fab';
import { defineCustomElement as defineCustomElementFabButton } from '@ionic/core/components/ion-fab-button';
import { defineCustomElement as defineCustomElementFabList } from '@ionic/core/components/ion-fab-list';
import { defineCustomElement as defineCustomElementIcon } from '@ionic/core/components/ion-icon';
import type { Meta, StoryObj } from '@storybook/web-components';
import { html } from 'lit';
import {
  TP_COLORS,
  TP_COLORS_MEDSOFT,
  TP_COLORS_RECURSOS,
} from '../../../../../utils/constants/color.constant';
import {
  TpColorAula,
  TpColorMaterial,
  TpColorOrange,
  TpColorProvas,
  TpColorQuestoes,
  TpColorRevalida,
} from '../../../../../utils/enums/color.enum';
import { TpIonFab } from '../../utils/fab.type';

defineCustomElementFab();
defineCustomElementFabList();
defineCustomElementFabButton();
defineCustomElementIcon();

const meta: Meta<
  Components.IonFab &
    Components.IonFabButton &
    Components.IonFabList &
    Components.IonIcon &
    TpIonFab
> = {
  title: 'components/Ionic/Fab/Website',
  argTypes: {
    color: {
      control: { type: 'select' },
      options: [...TP_COLORS, ...TP_COLORS_MEDSOFT, ...TP_COLORS_RECURSOS],
      description: `Define a cor do componente.

**OBS**: As cores **'${TpColorAula.Aula}'**, **'${TpColorMaterial.Material}'**, **'${TpColorQuestoes.Questoes}'**, **'${TpColorRevalida.Revalida}'** e **'${TpColorProvas.Provas}'** estão disponiveis apenas no theme **MedSoft**.

**OBS**: A cor **'${TpColorOrange.Orange}'** está disponivel apenas no theme **Recursos**.`,
      table: {
        defaultValue: {
          summary: 'undefined',
        },
        type: {
          summary: [
            ...TP_COLORS,
            ...TP_COLORS_MEDSOFT,
            ...TP_COLORS_RECURSOS,
          ].join('|'),
        },
      },
    },
  },
};

export default meta;

type Story = StoryObj<
  Components.IonFab &
    Components.IonFabButton &
    Components.IonFabList &
    Components.IonIcon &
    TpIonFab
>;

export const DefaultMarkup: Story = {
  render: () => html`
    <ion-fab slot="fixed" horizontal="center" vertical="center">
      <ion-fab-button>
        <ion-icon src="./assets/tp-plus.svg"></ion-icon>
      </ion-fab-button>

      <ion-fab-list side="top">
        <ion-fab-button>
          <ion-icon src="./assets/tp-star.svg"></ion-icon>
        </ion-fab-button>

        <ion-fab-button>
          <ion-icon src="./assets/tp-star.svg"></ion-icon>
        </ion-fab-button>

        <ion-fab-button>
          <ion-icon src="./assets/tp-star.svg"></ion-icon>
        </ion-fab-button>

        <ion-fab-button>
          <ion-icon src="./assets/tp-star.svg"></ion-icon>
        </ion-fab-button>
      </ion-fab-list>
    </ion-fab>
  `,
};
DefaultMarkup.argTypes = {
  color: { table: { disable: true } },
};

export const ColorsMarkup: Story = {
  render: ({ ...args }) => html`
    <ion-fab slot="fixed" horizontal="center" vertical="center">
      <ion-fab-button color="${args.color}">
        <ion-icon src="./assets/tp-plus.svg"></ion-icon>
      </ion-fab-button>

      <ion-fab-list side="top">
        <ion-fab-button>
          <ion-icon src="./assets/tp-star.svg"></ion-icon>
        </ion-fab-button>

        <ion-fab-button>
          <ion-icon src="./assets/tp-star.svg"></ion-icon>
        </ion-fab-button>

        <ion-fab-button>
          <ion-icon src="./assets/tp-star.svg"></ion-icon>
        </ion-fab-button>

        <ion-fab-button>
          <ion-icon src="./assets/tp-star.svg"></ion-icon>
        </ion-fab-button>
      </ion-fab-list>
    </ion-fab>
  `,
};
